import { Canvas, Image, Rect, filters } from 'fabric';
import { MediaUploader } from '../utils/MediaUploader';

export class TemplateEditor {

    constructor($container) {
        this.$container = $container;
        this.eventListeners = new Map();
        this.initialize();
        this.changeSelectionMode('image');
    }

    initialize() {
        this.setupToolbar();
        this.initializeEditor();
        this.initializeToolbar();
        this.bindCanvasEvents();
    }

    initializeEditor(){

        const $editorContainer = this.$editorContainer = this.$container.querySelector('.template-canvas-container');

        const $canvas = document.createElement('canvas');
        $editorContainer.appendChild($canvas);

        this.canvas = new Canvas($canvas, {
            width: 800,
            height: 500,
            backgroundColor: '#f0f0f0',
            preserveObjectStacking: true
        });

        this.initializeSafeZone();

    }

    initializeSafeZone(){

        this.safeZone = new Rect({
            width: 200,
            height: 200,
            fill: 'rgba(0, 124, 186, 0.2)',
            stroke: '#007cba',
            strokeWidth: 2,
            strokeDashArray: [5, 5],
            selectable: true,
            evented: true,
            hasControls: true,
            hasBorders: true,
            lockRotation: true,
            transparentCorners: false,
            cornerColor: '#007cba',
            cornerSize: 10,
            cornerStyle: 'circle',
            originX: 'center',
            originY: 'center',
            centeredScaling: true,
            left: (this.canvas.width / 2),
            top: (this.canvas.height / 2),
        });

        this.canvas.add(this.safeZone);

        if( this.state ){

            this.setState({
                safeZone: {
                    left: (this.canvas.width / 2) / this.canvas.width  * 100,
                    top: (this.canvas.height / 2) / this.canvas.height * 100,
                    width: 200,
                    height: 200,
                }
            });

        }

    }

    // Event System
    on(eventName, callback) {
        if (!this.eventListeners.has(eventName)) {
            this.eventListeners.set(eventName, new Set());
        }
        this.eventListeners.get(eventName).add(callback);
        return () => this.off(eventName, callback); // Return unsubscribe function
    }

    off(eventName, callback) {
        if (this.eventListeners.has(eventName)) {
            this.eventListeners.get(eventName).delete(callback);
        }
    }

    emit(eventName, data) {
        if (this.eventListeners.has(eventName)) {
            this.eventListeners.get(eventName).forEach(callback => callback(data));
        }
    }

    bindCanvasEvents() {
        this.canvas.on({
            'object:moving': (e) => {
                this.emit('stateChange', this.updateState());
                if (this.imageToolbar?.classList.contains('visible')) {
                    this.updateToolbarPosition();
                }
            },
            'object:scaling': (e) => {
                this.emit('stateChange', this.updateState());
                if (this.imageToolbar?.classList.contains('visible')) {
                    const obj = e.target;
                    this.widthInput.value = Math.round(obj.width * obj.scaleX);
                    this.heightInput.value = Math.round(obj.height * obj.scaleY);
                    this.updateToolbarPosition();
                }
            },
            'object:rotating': (e) => {
                this.emit('stateChange', this.updateState());
                if (this.imageToolbar?.classList.contains('visible')) {
                    this.updateToolbarPosition();
                }
            },
            'object:modified': (e) => {
                this.emit('stateChange', this.updateState());
            },
            'selection:created': (e) => {
                if (e.selected[0] === this.backgroundImage) {
                    this.showToolbar();
                }
            },
            'selection:cleared': () => {
                this.hideToolbar();
            },
            'selection:updated': (e) => {
                if (e.target === this.backgroundImage) {
                    this.showToolbar();
                } else {
                    this.hideToolbar();
                }
            }
        });
    }

    async loadImage(imageId) {

        const imageUrl = await MediaUploader.getImageUrl(imageId);
        const imgElement = new window.Image();
        imgElement.crossOrigin = 'anonymous';

        await new Promise((resolve, reject) => {
            imgElement.onload = () => {
                this.onImageLoadFinished(imgElement);
                resolve();
            };
            imgElement.onerror = reject;
            imgElement.src = imageUrl;
        });
        
    }

    onImageLoadFinished($image) {
        this.loadedImage = $image;
        
        // Center the image on load
        const canvasCenter = {
            x: this.canvas.width / 2,
            y: this.canvas.height / 2
        };

        // Calculate scale to fit canvas while maintaining aspect ratio
        const scale = Math.min(
            (this.canvas.width * 0.8) / $image.width,
            (this.canvas.height * 0.8) / $image.height
        );
        
        this.renderImage(canvasCenter, scale);
    }

    renderImage(center = null, scale = 1) {

        if(!this.loadedImage) return;
        if (this.backgroundImage) this.canvas.remove(this.backgroundImage);

        const imageCanvas = this.getImageCanvas();
        const fabricImage = new Image(imageCanvas);

        let left = this.canvas.width / 2;
        let top = this.canvas.height / 2;

        if(this.state.imageZone) {
            left = this.state.imageZone.left * this.canvas.width / 100;
            top = this.state.imageZone.top * this.canvas.height / 100;
        }

        // Configure image properties
        fabricImage.set({
            ...this.state.imageZone,
            selectable: true,
            evented: true,
            hasControls: true,
            hasBorders: true,
            lockUniScaling: false,
            cornerStyle: 'circle',
            cornerColor: '#007cba',
            cornerSize: 10,
            transparentCorners: false,
            centeredScaling: true,
            originX: 'center',
            originY: 'center',
            left,
            top,
        });

        if (this.state.colorOverlayEnabled){

            fabricImage.filters.push(new filters.BlendColor({
                color: this.state.overlayColor,
                mode: 'multiply',
                alpha: this.state.overlayOpacity || 0.5
            }));
            fabricImage.applyFilters();

        }

        this.backgroundImage = fabricImage;
        this.canvas.remove(this.safeZone);
        
        // Add objects in correct order
        this.canvas.add(fabricImage);
        this.canvas.add(this.safeZone);

        // Set coordinates and interactivity
        fabricImage.setCoords();
        this.safeZone.setCoords();

        fabricImage.selectable = (this.activeMode === 'image');
        fabricImage.evented = (this.activeMode === 'image');

        this.canvas.requestRenderAll();
        this.emit('stateChange', this.updateState());

        this.updateObjectsSelectability();
    }

    getImageCanvas() {
        // Create main rendering canvas
        const $renderingCanvas = document.createElement('canvas');
        const renderContext = $renderingCanvas.getContext('2d');
        $renderingCanvas.width = this.loadedImage.width;
        $renderingCanvas.height = this.loadedImage.height;

        // Draw original image
        renderContext.drawImage(this.loadedImage, 0, 0);

        return $renderingCanvas;
    }

    setColor( color ){
        this.overlayColor = color;
        this.renderImage();
        this.emit('stateChange', state);
    }

    setOpacity( opacity ){
        this.opacity = opacity;
        this.renderImage();
    }

    zoom(factor){
        let zoom = this.canvas.getZoom() * factor;
        zoom = Math.min(Math.max(0.1, zoom), 5);
        
        const center = {
            x: this.canvas.width / 2,
            y: this.canvas.height / 2
        };
        
        this.canvas.zoomToPoint(center, zoom);
    }

    resetView(){
        this.canvas.setViewportTransform([1, 0, 0, 1, 0, 0]);
    }

    changeSelectionMode(mode){

        this.selectionMode = mode;
        for( const $button of this.$changeModeButtons ){
            $button.classList.toggle('active', mode == $button.dataset.mode);
        }

        this.updateObjectsSelectability();

    }

    updateObjectsSelectability() {
        // Update safe zone selectability
        if (this.safeZone) {
            this.safeZone.selectable = (this.selectionMode === 'safezone');
            this.safeZone.evented = (this.selectionMode === 'safezone');
        }

        // Update background image selectability
        if (this.backgroundImage) {
            this.backgroundImage.selectable = (this.selectionMode === 'image');
            this.backgroundImage.evented = (this.selectionMode === 'image');
            this.backgroundImage.hasControls = (this.selectionMode === 'image');
            this.backgroundImage.hasBorders = (this.selectionMode === 'image');
        }
    }

    setupToolbar(){

        const $toolbar = this.$container.querySelector('.template-editor-toolbar');

        this.$changeModeButtons = $toolbar.querySelectorAll('.mode-select');
        for( const $button of this.$changeModeButtons ){
            $button.addEventListener('click', (e) => {
                this.changeSelectionMode(e.currentTarget.dataset.mode);
            });
        }

        const $zoomInButton = $toolbar.querySelector('.zoomIn');
        $zoomInButton.addEventListener('click', () => this.zoom(1.1));

        const $zoomOutButton = $toolbar.querySelector('.zoomOut');
        $zoomOutButton.addEventListener('click', () => this.zoom(0.9));

        const $resetZoomButton = $toolbar.querySelector('.resetZoom');
        $resetZoomButton.addEventListener('click', () => this.resetView());

        const $resetImageButton = $toolbar.querySelector('.resetImage');
        $resetImageButton.addEventListener('click', () => this.resetImage());

        const $resetSafeZoneButton = $toolbar.querySelector('.resetSafeZone');
        $resetSafeZoneButton.addEventListener('click', () => this.resetSafeZone());

    }

    resetImage() {

        if( !this.originalState ) return;

        this.setState(this.originalState);

    }

    resetSafeZone() {
        if(this.safeZone) this.canvas.remove(this.safeZone);
        this.initializeSafeZone();
    }

    updateState() {
        if (!this.canvas) return null;

        const state = {
            safeZone: {
                left: (this.safeZone.left / this.canvas.width) * 100,
                top: (this.safeZone.top / this.canvas.height) * 100,
                width: Math.round(this.safeZone.getScaledWidth()),
                height: Math.round(this.safeZone.getScaledHeight())
            }
        };

        if (this.backgroundImage) {
            state.imageZone = {
                left: (this.backgroundImage.left / this.canvas.width) * 100,
                top: (this.backgroundImage.top / this.canvas.height) * 100,
                scaleX: this.backgroundImage.scaleX,
                scaleY: this.backgroundImage.scaleY,
                angle: this.backgroundImage.angle,
            };
        }

        this.state = Object.assign(this.state ?? {}, state);

        return state;
    }

    async setState(state) {

        //Merge current state with the new one
        if( !this.state ) this.originalState = Object.assign({}, state);
        this.state = Object.assign(this.state ?? {}, state);

        if (state.safeZone) this.setSafeZoneAttributes(state.safeZone);
        if( state.imageZone ) this.setImageZoneAttributes(state.imageZone);
        if (state.image) await this.loadImage(state.image);

        if( (state.overlayColor != undefined
            || state.overlayOpacity != undefined 
            || state.colorOverlayEnabled != undefined) && !state.image ) this.renderImage();

        this.emit('stateChange', this.state);
        this.canvas.requestRenderAll();

    }

    setSafeZoneAttributes( attributes ){

        let finalAttributes = Object.assign({}, attributes);
        if(attributes.left) finalAttributes.left = attributes.left * this.canvas.width / 100;
        if(attributes.top) finalAttributes.top = attributes.top * this.canvas.height / 100;
        this.safeZone.set(finalAttributes);
        this.safeZone.setCoords();
        
    }

    setImageZoneAttributes( attributes ){
        if(!this.backgroundImage) return;

        let finalAttributes = Object.assign({}, attributes);
        if(attributes.left) finalAttributes.left = attributes.left * this.canvas.width / 100;
        if(attributes.top) finalAttributes.top = attributes.top * this.canvas.height / 100;

        this.backgroundImage.set(finalAttributes);
        this.backgroundImage.setCoords();
    }

    initializeToolbar() {
        // Clone toolbar template
        const toolbarTemplate = document.getElementById('designer-image-toolbar-template');
        this.imageToolbar = toolbarTemplate.content.cloneNode(true).querySelector('.designer-image-toolbar');
        this.$editorContainer.appendChild(this.imageToolbar);
    
        // Store references to inputs
        this.widthInput = this.imageToolbar.querySelector('.width-input');
        this.heightInput = this.imageToolbar.querySelector('.height-input');
        this.pixelToCmLabel = this.imageToolbar.querySelector('.pixel-to-cm');
    
        // Add toolbar button handlers
        this.imageToolbar.querySelector('.center-image').addEventListener('click', (e) => {
            const activeObject = this.canvas.getActiveObject();
            if (activeObject) {
                // Center the object on canvas
                activeObject.set({
                    left: this.canvas.width / 2,
                    top: this.canvas.height / 2
                });
                
                activeObject.setCoords();
                this.canvas.renderAll();
                
                // Update toolbar position
                this.updateToolbarPosition();
            }
            e.preventDefault();
        });

        // Add width/height input handlers
        this.widthInput.addEventListener('input', (e) => this.handleDimensionChange('width', e.target.value));
        this.heightInput.addEventListener('input', (e) => this.handleDimensionChange('height', e.target.value));

        // Update DPI conversion display
        this.updatePixelToCmConversion();
    }

    handleDimensionChange(dimension, value) {
        const activeObject = this.canvas.getActiveObject();
        if (!activeObject) return;

        const pixelValue = parseInt(value, 10);
        if (isNaN(pixelValue)) return;

        const currentWidth = activeObject.width * activeObject.scaleX;
        const currentHeight = activeObject.height * activeObject.scaleY;
        const aspectRatio = activeObject.width / activeObject.height;

        let newScaleX, newScaleY;

        if (dimension === 'width') {
            newScaleX = pixelValue / activeObject.width;
            newScaleY = newScaleX; // Maintain aspect ratio
        } else {
            newScaleY = pixelValue / activeObject.height;
            newScaleX = newScaleY; // Maintain aspect ratio
        }

        // Apply new scale
        activeObject.set({
            scaleX: newScaleX,
            scaleY: newScaleY
        });

        // Update the other input
        if (dimension === 'width') {
            this.heightInput.value = Math.round(activeObject.height * newScaleY);
        } else {
            this.widthInput.value = Math.round(activeObject.width * newScaleX);
        }

        activeObject.setCoords();
        this.canvas.renderAll();
        this.updateToolbarPosition();
        this.canvas.fire('object:modified', { target: activeObject });
    }

    updatePixelToCmConversion() {
        // Get DPI from WordPress settings
        const dpi = window.octoPrintDesigner?.dpi || 300; // Default to 300 if not set
        const pixelsPerCm = Math.round((dpi / 2.54)); // 2.54 cm = 1 inch
        this.pixelToCmLabel.textContent = `1cm  ${pixelsPerCm}px`;
    }

    showToolbar() {
        const activeObject = this.canvas.getActiveObject();
        if (!activeObject) return;

        // Update dimension inputs
        this.widthInput.value = Math.round(activeObject.width * activeObject.scaleX);
        this.heightInput.value = Math.round(activeObject.height * activeObject.scaleY);

        this.imageToolbar.classList.add('visible');
        this.updateToolbarPosition();
    }

    hideToolbar() {
        this.imageToolbar.classList.remove('visible');
    }

    updateToolbarPosition() {
        const activeObject = this.canvas.getActiveObject();
        if (!activeObject) return;
    
        // Get canvas container rect
        const containerRect = this.canvas.wrapperEl.getBoundingClientRect();
        
        // Get object coordinates relative to canvas
        const objCoords = activeObject.getBoundingRect();
        const zoom = this.canvas.getZoom();
        const pan = this.canvas.viewportTransform;
    
        // Calculate absolute position considering zoom and pan
        const absoluteLeft = (objCoords.left * zoom) + pan[4];
        const absoluteTop = (objCoords.top * zoom) + pan[5];
        const absoluteWidth = objCoords.width * zoom;
        
        // Position the toolbar relative to the canvas container
        this.imageToolbar.style.left = `${absoluteLeft + (absoluteWidth / 2)}px`;
        this.imageToolbar.style.top = `${absoluteTop - this.imageToolbar.offsetHeight - 10}px`;
    }

}