import { ErrorHandler } from '../utils/ErrorHandler';

export class SizesManager {
    constructor() {
        this.container = document.getElementById('template-sizes-container');
        if (!this.container) return;
        
        this.sizesList = this.container.querySelector('.sizes-list');
        this.addButton = this.container.querySelector('.add-size');
        
        this.init();
    }

    init() {
        if (this.addButton) {
            this.addButton.addEventListener('click', () => this.addSize());
        }

        if (this.sizesList) {
            this.sizesList.addEventListener('click', (e) => {
                if (e.target.classList.contains('remove-size')) {
                    this.removeSize(e.target.closest('.size-item'));
                }
            });

            // Make sizes sortable
            if (window.jQuery && window.jQuery.ui) {
                window.jQuery(this.sizesList).sortable({
                    items: '.size-item',
                    handle: '.dashicons-move',
                    axis: 'y',
                    update: () => this.updateSizeOrders()
                });
            }

            // Add validation listeners
            this.sizesList.addEventListener('input', (e) => {
                if (e.target.matches('input[name*="[id]"]')) {
                    this.validateSizeId(e.target);
                }
            });
        }
    }

    addSize() {
        try {
            const index = this.sizesList.children.length;
            const sizeItem = this.createSizeElement(index);
            this.sizesList.appendChild(sizeItem);
            this.updateSizeOrders();
        } catch (error) {
            ErrorHandler.handle(error, 'Add Size');
        }
    }

    createSizeElement(index) {
        const div = document.createElement('div');
        div.className = 'size-item';
        div.dataset.index = index;
        
        div.innerHTML = `
            <span class="dashicons dashicons-move"></span>
            <input type="text" 
                   name="sizes[${index}][id]" 
                   placeholder="${window.wp.i18n.__('Size ID (e.g., S, M, L)', 'octo-print-designer')}" 
                   pattern="[A-Za-z0-9_-]+"
                   required>
            <input type="text" 
                   name="sizes[${index}][name]" 
                   placeholder="${window.wp.i18n.__('Size Name', 'octo-print-designer')}"
                   required>
            <input type="number" 
                   name="sizes[${index}][order]" 
                   value="${index}"
                   class="small-text"
                   min="0">
            <button type="button" class="button remove-size">
                ${window.wp.i18n.__('Remove', 'octo-print-designer')}
            </button>
        `;
        
        return div;
    }

    removeSize(sizeItem) {
        try {
            const sizeId = sizeItem.querySelector('input[name*="[id]"]').value;
            
            // Check if this size is being used in any variations
            const variationsContainer = document.getElementById('template-variations-container');
            if (variationsContainer) {
                const usedInVariation = Array.from(variationsContainer.querySelectorAll(`input[name*="[available_sizes][]"][value="${sizeId}"]:checked`));
                if (usedInVariation.length > 0) {
                    if (!confirm(window.wp.i18n.__('This size is used in one or more variations. Removing it will also remove it from those variations. Continue?', 'octo-print-designer'))) {
                        return;
                    }
                    // Remove size from variations
                    usedInVariation.forEach(checkbox => {
                        checkbox.checked = false;
                    });
                }
            }
            
            sizeItem.remove();
            this.reindexSizes();
            this.updateSizeOrders();
        } catch (error) {
            ErrorHandler.handle(error, 'Remove Size');
        }
    }

    reindexSizes() {
        Array.from(this.sizesList.children).forEach((item, index) => {
            item.dataset.index = index;
            
            const inputs = item.querySelectorAll('input');
            inputs.forEach(input => {
                const name = input.getAttribute('name');
                if (name) {
                    input.setAttribute('name', name.replace(/sizes\[\d+\]/, `sizes[${index}]`));
                }
            });
        });
    }

    updateSizeOrders() {
        Array.from(this.sizesList.children).forEach((item, index) => {
            const orderInput = item.querySelector('input[name*="[order]"]');
            if (orderInput) {
                orderInput.value = index;
            }
        });
    }

    validateSizeId(input) {
        // Remove any invalid characters
        input.value = input.value.replace(/[^A-Za-z0-9_-]/g, '');

        // Check for duplicates
        const sizeIds = Array.from(this.sizesList.querySelectorAll('input[name*="[id]"]'))
            .map(input => input.value)
            .filter(Boolean);

        const duplicates = sizeIds.filter(
            (id, index) => sizeIds.indexOf(id) !== index
        );

        if (duplicates.includes(input.value)) {
            input.setCustomValidity(window.wp.i18n.__('This size ID is already in use', 'octo-print-designer'));
        } else {
            input.setCustomValidity('');
        }
    }
}