export class MediaUploader {

    static async open(options = {}) {
        if (!wp?.media) {
            throw new Error('WordPress Media Library not available');
        }

        return new Promise((resolve, reject) => {
            const frame = wp.media({
                title: options.title || 'Select Media',
                button: { text: options.buttonText || 'Select' },
                multiple: false,
                library: { type: 'image' }
            });

            frame.on('select', () => {
                const selection = frame.state().get('selection');
                const attachment = selection.first().toJSON();

                if (!attachment.type || attachment.type !== 'image') {
                    reject(new Error('Please select an image file'));
                    return;
                }

                resolve({
                    id: attachment.id,
                    url: attachment.url,
                    width: attachment.width,
                    height: attachment.height,
                    alt: attachment.alt || ''
                });
            });

            frame.on('close', () => {
                const selection = frame.state().get('selection');
                if (!selection.length) {
                    reject(new Error('No media selected'));
                }
            });

            frame.open();
        });
    }

    static async getImageUrl(attachmentId) {
        const attachment = await wp.media.attachment(attachmentId);
        if(!attachment?.attributes?.url) await attachment.fetch();
        return attachment.attributes.url;
    }

}